@import "../../../../Styles/main.scss";

.themes-section {
  background-color: $color-bg-light;
}

.select-theme {
  text-align: center;
  max-width: 25rem;
  margin: 0 auto;

  &__title {
    font-size: $heading-4;
    margin-bottom: $spacer-small;
    text-transform: uppercase;
  }

  &__button {
    @include button($color-dark-blue);

    color: $color-text-light;
    text-transform: uppercase;
    font-weight: bolder;
    font-size: $default-3;
    margin-bottom: $spacer-small;
  }

  &__generate-button {
    @include button($color-green);

    display: inline-block;
    color: $color-text-light;
    font-size: $default-3;
    font-weight: bolder;
    text-transform: uppercase;
  }
}

.select-theme-modal {
  &__modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $color-bg-dark;
    padding: 1rem 2rem;
    padding-top: 0;
    border-radius: 2rem;
    max-height: 55rem;
    max-width: 90rem;
    width: 100vw;
    height: 90vh;
    border: 0;
    outline: none;
    overflow-y: scroll;
  }

  @media only screen and (max-width: $mobile-width) {
    &__modal {
      top: 10vh;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0;
      transform: translate(0, 0);
      border-radius: 2rem 2rem 0 0;
      padding-top: 0;
    }
  }

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba($color-bg-light, 0.75);
  }

  &__top-controls {
    position: sticky;
    right: 2rem;
    top: 0rem;
    text-align: right;
    background-color: $color-bg-dark;
    margin-bottom: 0.25rem;
    padding: 0.5rem 0 0 0;
  }
}
